import * as React from 'react';
import * as draftStorage from '../draftStorage';
import { TagInput, TagState } from './TagInput';
import { ToolSection } from '../framework/ToolSection';
import { SimpleSplitEditor, SplitMode, SplitModeSelector, NoteModeSelector } from './SimpleSplitEditor';
import { isMobile } from './Layout';
import { db } from '../db/db';
import { Reference, NoteMode, getNoteTitle } from '../db/note';
import { TreeEditor } from './TreeEditor';
import { SettingsCompactor } from './SettingsCompactor';
import './NoteEditor.scss';
import { Loader } from '../components/Loader';
import { ContextSelector } from '../components/ContextSelector';
import { Data } from '../model';
import moment from 'moment';

export function NoteEditor(props: {onNoteAdded: () => void, id?: string, title?: string, text?: string, tags?: string, contextId: string|null, availableTags: string[], noteFor: Reference|null, noteMode?: NoteMode, data: Data, dailyDate: string|null}) {

    const [loaded, setLoaded] = React.useState(false);

    const [noteState, setNoteState] = React.useState({
        title: '',
        text: '',
        tags: '',
        tagState: new TagState([], []),
        splitMode: isMobile() ? SplitMode.Editor : SplitMode.Split,
        noteMode: 'tree' as NoteMode,
        contextId: null as string|null,
        dailyDate: null as string|null,
    });

    React.useEffect(() => {
        async function onMount() {
            const draft = await draftStorage.getDraft(props.id);

            const title = props.title;
            let text = props.text;
            const tags = props.tags;
            const noteMode = props.noteMode;
            const contextId = props.contextId;
            const dailyDate = props.dailyDate;

            let state: any = {};

            if (draft !== null && draft !== text && draft.length > 0) {
                text = draft;
            }

            if (title !== undefined) {
                state.title = title;
            }

            if (text !== undefined) {
                state.text = text;
            }

            if (noteMode !== undefined) {
                state.noteMode = noteMode;
            }

            if (contextId !== undefined) {
                state.contextId = contextId;
            }

            if (dailyDate !== undefined) {
                state.dailyDate = dailyDate;
            }

            state.tagState = noteState.tagState
                .withAvailableTags(props.availableTags);

            if (tags && tags.length > 0) {
                state.tagState = state.tagState.withSelectedTags(tags.split(' '));
            }

            setNoteState(state);
            setLoaded(true);
        }

        onMount();
        // eslint-disable-next-line
     }, []);

    function onTitleChanged(e: React.ChangeEvent<HTMLInputElement>) {
        setNoteState({ ...noteState, title: e.target.value });
    }

    function onTextChanged(text: string) {
        if (text !== props.text) {
            draftStorage.setDraft(props.id, text);
        }

        setNoteState({ ...noteState, text });
    }

    async function onSave() {
        const id = props.id || null;

        if (id) {
            let note = await db.notes.get(id);

            if (note === null) {
                throw new Error('Note is null?');
            }

            note.title = noteState.title;
            note.content = noteState.text;
            note.tags = noteState.tagState.getTags();
            note.mode = noteState.noteMode;
            note.contextId = noteState.contextId;
            note.dailyDate = noteState.dailyDate;

            await db.notes.save(note);
        } else {
            await db.notes.create({
                title: noteState.title,
                content: noteState.text,
                tags: noteState.tagState.getTags(),
                noteFor: null,
                mode: noteState.noteMode,
                contextId: noteState.contextId,
                dailyDate: noteState.dailyDate
            });
        }

        await draftStorage.clearDraft(props.id);
        props.onNoteAdded();
    }

    const updateDailyDate = (value: string) => {
        if (value === '') {
            setNoteState({ ...noteState, dailyDate: null });
        } else {
            setNoteState({ ...noteState, dailyDate: new Date(value).toISOString()});
        }
    }

    if (!loaded) {
        return <Loader />;
    }

    return (
        <div style={{width: '100%', flex: 1, display: 'flex', flexDirection: 'column'}}>
            <ToolSection verticalAround={false}>
                <SettingsCompactor wideStyle={{justifyContent: 'space-between'}} alwaysVisible={() => (
                    <>
                        {props.id === undefined ?
                            <button className="button is-primary" onClick={onSave}>Add note</button> :
                            <button className="button is-primary" onClick={onSave}>Update note</button>}
                        <ContextSelector contextId={noteState.contextId} data={props.data} onChange={contextId => setNoteState({ ...noteState, contextId })} />
                        <input type="date" className="input is-large" value={(noteState.dailyDate && moment(noteState.dailyDate).format('YYYY-MM-DD')) || ''} onChange={e => updateDailyDate(e.target.value)} />
                    </>
                )}>
                    <NoteModeSelector mode={noteState.noteMode} onModeChange={noteMode => setNoteState({ ...noteState, noteMode })} />
                    <SplitModeSelector mode={noteState.splitMode} onModeChange={splitMode => setNoteState({ ...noteState, splitMode })} />
                </SettingsCompactor>
            </ToolSection>
            <ToolSection>
                {!props.noteFor && !noteState.dailyDate && (
                    <>
                        <input type="text" placeholder="Title" className="input is-large is-wide" value={noteState.title} onChange={onTitleChanged} />
                        <TagInput tagState={noteState.tagState} onChange={s => setNoteState({ ...noteState, tagState: s })} />
                    </>
                )}
                {noteState.dailyDate && (
                    <h1>{getNoteTitle({ dailyDate: noteState.dailyDate, title: noteState.title, content: noteState.text })}</h1>
                )}
            </ToolSection>
            <div style={{flex: 1}}>
                {noteState.noteMode === 'markdown' ? (
                    <SimpleSplitEditor text={noteState.text} expand={true} onTextChanged={onTextChanged} mode={noteState.splitMode} data={props.data} contextId={noteState.contextId} />
                ) : (
                    <TreeEditor text={noteState.text} onTextChanged={onTextChanged} contextId={noteState.contextId} />
                )}
            </div>
        </div>
    );
}